//const Svg = props => ({ stroke = false, color = ``, width, icon, left, top, hiddenMobile = false }: SVGProps) => (
const Svg = props => {

    const [svgState, setSvgState] = useState(props)

    return(  
    <svg
        sx={{
          position: `absolute`,
          stroke: svgState.stroke ? `currentColor` : `none`,
          fill: svgState.stroke ? `none` : `currentColor`,
        //   display: hiddenMobile ? hidden : `block`,
        display:svgState.hiddenMobile,
          color:svgState.color,
          width:10,
          left:svgState.left,
          top:svgState.top,
        }}
        viewBox={icons[svgState.icon].viewBox}
      >
        {icons[svgState.icon].shape}
      </svg>);

    }

export default Svg